@import url('https://fonts.googleapis.com/css2?family=PT+Serif:wght@400;700&family=Roboto+Condensed:wght@400;700&display=swap&subset=cyrillic');

@import 'normalize';
@import 'variables';
@import 'mixins';
@import 'slick';
@import 'slick-theme';

* {
    box-sizing: border-box;
    margin: 0;
    padding: 0;
}

html {
    height: 100%;
    font-size: 10px;
}

body {
    min-height: 100%;
    color: #282828;
    font-family: 'Roboto Condensed', sans-serif;
    font-weight: 400;
    font-size: 18px;
    line-height: 24px;
    // font-family: 'PT Serif', serif;
}

a,
button {
    transition: all .4s ease-in;
    text-decoration: none;
    cursor: pointer;
}

.wrapper {
    height: 100%;
    width: 100%;
  }
    
  .wrapper { display: table; }
  
  .cell {
    display: table-cell;
    margin: 0;
    padding: 0;
    text-align: center;
    vertical-align: middle;
  }
  
  #holder {
    width: 603px;
    height: 100px;
    margin: auto;
  }
  
  .cntSeparator { display: none; }
  
  .cntDigit {
    margin-top: 38px !important;
  }
  
    .cntDigit#cnt_0 { margin-left: 52px; }
    .cntDigit#cnt_2 { margin-left: 7px; }
  

