// section cover

.slick-arrow {
    position: absolute;
    border-radius: 50%;
    z-index: 3;
    top: 50%;
    transform: translateY(-50%);
    outline: none;
    border: none;
    background-color: transparent;
    width: 70px;
    cursor: pointer;
    &--prev {
        left: 20px;
    }
    &--next {
        right: 20px;
    }
}

.slick-arrow--prev__img,
.slick-arrow--next__img {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    min-width: 100%;
}

.search {
    text-align: center;
    &__inner {
        margin: 60px auto 70px;
    }
    &__form {
        display: flex;
        justify-content: center;
        align-items: center;
        &--label {
            font-size: 24px;
            line-height: 24px;
            color: #888;
            margin-right: 69px;
            width: 156px;
            text-align: left;
        }
        &--input {
            padding: 23px 15px;
            background: #FFFFFF;
            border: 2px solid #C4C4C4;
            font-size: 18px;
            border-right: none;
            width: 430px;
            max-width: 430px;
            &::placeholder {
                font-family: Roboto Condensed;
                color: #888;
                font-size: 18px;
                line-height: 24px;
                font-weight: normal;
            }
        }
        &--button {
            display: flex;
            align-items: center;
            justify-content: center;
            text-align: center;
            padding: 23px;
            color: #FFFFFF;
            background-color: #C4C4C4;
            border: 2px solid #C4C4C4;
            box-sizing: border-box;
            cursor: pointer;
            &:hover,
            &:focus {
                background-color: #282828;
                border: 2px solid #282828;
                color: #fff;
                outline: none;
            }
        }
        .fab {
            margin-right: 9px;
        }
        
    }
}

// section popular

.popular {
    text-align: center;
    &__title {
        font-family: PT Serif;
        font-size: 48px;
        line-height: 64px;
        font-weight: 400;
        color: #fff;
        background-color: #000;
        padding: 18px;
    }
    &__inner {
        display: flex;
        justify-content: space-between;
        padding: 100px 0;
        flex-wrap: wrap;
    }
    &__item{
        text-align: center;

    }
    &__img {
        width: 280px;
        height: 280px;
        max-width: 100%;
        max-height: 100%;
        border-radius: 50%;
        object-fit: cover;
    }
    &__inner-title {
        font-size: 24px;
        line-height: 24px;
        font-weight: 400;
        color: #888;
        padding: 30px 10px;
    }
    &__link {
        background-color: #c4c4c4;
        padding: 13px 48px;
        color: #fff;
        &:hover,
        &:focus {
            background-color: #282828;
            color: #fff;
        }
    }
}

// section reason

.reason {
    padding: 100px 0;
    &__inner {
       display: flex;
       justify-content: space-between;
    }
    &__item {
        width: 470px;
    }
    &__title {
        font-family: PT Serif;
        font-size: 36px;
        line-height: 36px;
        font-weight: 400;
        margin-bottom: 20px;
        padding-left: 136px;
        position: relative;
        &::before {
            content: '';
            position: absolute;
            top: 0;
            left: 0;
            width: 103px;
            height: 103px;
        }
        &--icon-1 {
            &::before {
                background-image: url(../img/clock.svg);
            }
        }
        &--icon-2 {
            &::before {
                background-image: url(../img/globe.svg);
            }
        }
    }
    &__text {
        font-size: 18px;
        line-height: 24px;
        margin-bottom: 42px;
    }
    &__link {
        font-size: 18px;
        line-height: 24px;
        color: #888888;
        text-decoration: underline;
        padding-left: 36px;
        position: relative;
        z-index: 5;
        &:hover,
        &:focus {
            color: #fff;
            &::before {
                background-image: url(../img/arrow-next.png);
                background-size: cover;
            }
            &::after {
                content: '';
                position: absolute;
                height: 25px;
                width: 90px;
                background-color: #282828;
                top: 0;
                left: 33px;
                transition: all .3s ease-in-out;
                z-index: -1;
            }
        }
        &::before {
            content: '';
            position: absolute;
            left: 0;
            top: -4px;
            width: 30px;
            height: 30px;
            background-image: url(../img/arrow-black.svg);
        }
    }
}

// section best

.best {
    background-size: cover;
    background-repeat: no-repeat;
    background-position: center;
    min-height: 100vh;
    text-align: right;
    &__title {
        max-width: 400px;
        font-family: PT Serif;
        font-size: 48px;
        line-height: 48px;
        font-weight: 400;
        color: #fff;
        padding-top: 68px;
        padding-bottom: 15px;
        margin-left: auto;
    }
    &__link {
        display: inline-block;
        color: #fff;
        background-color: #282828;
        font-weight: 400;
        padding: 16px 18px;
        &:hover,
        &:focus {
            background-color: #c4c4c4;
            color: #fff;
            font-weight: 400;
        }
    }
}

// section about

.about {
    padding: 100px 0;
    background-color: #282828;
    color: #fff;
    ul {
        list-style: none;
    }
    &__inner {
       display: flex;
       justify-content: space-between;
    }
    &__title {
        margin-bottom: 47px;
        font-family: PT Serif;
        font-size: 48px;
        line-height: 48px;
        font-weight: 400;
        text-align: center;
    }
    .our-managers {
        flex-basis: 25%;
        &__title {
           margin-bottom: 27px;
        }
        &__items {
            display: flex;
            justify-content: space-between;
            text-align: center;
        }
        &__item {
            flex-basis: 33.33333%;
        }
        &__img {
            max-width: 100%;
            max-height: 100%;
            border-radius: 50%;
            object-fit: cover;
            margin-bottom: 22px;
        }
        &__list {
            line-height: 21px;
            margin-bottom: 43px;
        }
        &__btn {
            border: none;
            color: #282828;
            background-color: #fff;
            padding: 16px 21px;
            line-height: 18px;
            &:hover,
            &:focus {
                background-color: #c4c4c4;
                color: #fff;
                font-weight: 400;
           }
        }
    }
    .blog {
        flex-basis: 34.2%;
         &__link {
            display: inline-block;
            line-height: 18px;
            color: #fff;
            padding-left: 67px;
            white-space: nowrap;
            margin-bottom: 28px;
            position: relative;
            &:hover,
            &:focus {
                color: #c4c4c4;
                &::before {
                    opacity: .6;
                    transition: all .4s ease-in;
                }
            }
            &::before {
                content: '';
                width: 30px;
                height: 30px;
                background-image: url(../img/arrow-white.svg);
                position: absolute;
                left: 0;
                top: -6px;
            }
        }
    }
    .reviews {
        flex-basis: 25%;
        &__img {
            width: 300px;
            height: 127px;
            max-height: 100%;
            max-width: 100%;
            margin-bottom: 22px;
            object-fit: cover;
        }
        &__text {
            line-height: 21px;
            margin-bottom: 22px;
        }
        &__link {
            display: inline-block;
            border: none;
            color: #282828;
            background-color: #fff;
            padding: 16px 28px;
            line-height: 18px;
            &:hover,
            &:focus {
                background-color: #c4c4c4;
                color: #fff;
                font-weight: 400;
            }
        }
    }

}

// footer

.footer {
    .footer-top {
        background-color: #282828;
        padding: 100px 0;
        color: #fff;
        &__inner {
           display: flex;
           align-items: center;
        }
    }
     &__menu-list {
        list-style: none;
        width: 250px;
        margin-right: 50px;
    }
     &__menu-link {
        color: #fff;
        font-size: 24px;
        font-weight: 400;
        line-height: 36px;
          
    }
    &__form {
         display: flex;
         align-items: center;
    }
    &__form-label {
        line-height: 18px;
        color: #fff;
        margin-right: 10px;
    }
    &__form-input {
        background-color: #FFFFFF;
        border: 2px solid #C4C4C4;
        height: 70px;
        width: 511px;
        max-width: 511px;
        padding: 0 15px;
        border: none;
        font-family: Roboto Condensed;
        font-size: 18px;
        line-height: 24px;
        font-weight: 400;
        color: #888888;
        &::placeholder {
            font-family: Roboto Condensed;
            font-size: 18px;
            line-height: 24px;
            font-weight: 400;
            color: #888888;
        }
    }
    &__form-btn {
        line-height: 18px;
        border: none;
        background-color: #c4c4c4;
        padding: 26px 35px;
        color: #fff;
    }
    .footer-bottom {
        background-color: #fff;
    }
    &__logo {
        text-align: center;
        padding: 3px 0 23px;
    }
}

@media (max-width: 1200px) {
    .popular {
        &__item {
            width: 50%;
            margin-bottom: 50px;
        }
        &__img {
            width: 400px;
            height: 400px;
        }
    } 
    .best {
        min-height: 80vh;
    }
   }

@media (max-width: 1140px)  {
    .about {
        &__inner {
            flex-wrap: wrap;
        }
        .reviews {
            width: 100%;
            margin-top: 50px;
        }
        .blog {
             margin-left: 150px;
        }
    }

    .footer {
        .footer-top {
            &__inner {
                flex-wrap: wrap;
                justify-content: center;
            }
        }
        &__menu {
            width: 100%;
            margin-bottom: 50px;
        }
        &__menu-list {
            text-align: center;
            margin: 0 auto;
        }
    }
}


@media (max-width: 992px) {
    .search__form {
        flex-wrap: wrap;
    }
    .search__form--label {
        width: 100%;
        text-align: center;
        margin-bottom: 20px;
        margin-right: 0;
    }
    .popular__img {
            width: 280px;
            height: 280px;
    }
    .reason {
        &__inner {
            flex-wrap: wrap;
        }
        &__item {
            width: 100%;
            margin-bottom: 50px;
        }
        &__title {
            min-height: 60px;
            padding-left: 60px;
            &::before {
                width: 50px;
                height: 50px;
                background-size: cover;
                top: 50%;
                transform: translateY(-50%);
            }
        } 
    }
    .best {
        min-height: 600px;
    }

} 

@media (max-width: 900px) {
   .about {
       &__inner {
           display: block;
           .our-managers {
               margin: 0 auto;
               &__item {
                   flex-basis: 48%;
               }
           }
           .blog {
               margin-left: 0;
               margin: 50px auto;
               max-width: 420px;
               width: auto;
           }
           .reviews {
              margin: 0 auto;
              max-width: 420px;
           }
       }
   } 
   .footer {
       &__form {
           flex-direction: column;
       }
       &__form-label {
           text-align: center;
           margin-bottom: 20px;
           margin-right: 0;
       }
       &__form-input {
        margin-bottom: 20px;
        text-align: center;
        width: 300px;
        // max-width: 60%;
       }
   }
}


@media (max-width: 768px) {
     .header__logo {
         margin: 40px 0 30px;
     }
     .logo__image {
         max-width: 250px;
     }
     .menu .list__link {
        padding: 24px 10px 28px;
     }
     .slider__item {
         min-height: 400px;
     }
     .popular {
         &__item {
             width: 100%;
         }
     }
     .best {
        min-height: 400px;
    }
    .footer .footer-top,
    .about,
    .reason,
    .popular__inner {
       padding: 50px 0;
    }
}

@media (max-width: 600px) {
    .header__logo {
        margin: 20px 0;
    }
    .logo__image {
        max-width: 200px;
    }
    .menu {
        border: none;
        .list {
           display: block;
           &__item {
               min-width: 100%;
               border-top: 2px solid #c4c4c4;
               &:nth-child(4) a {
                   border: none;
               }
           }
           &__link {
               min-width: 100%;
               border: none;
               padding: 20px 10px;
           }
       }
    }
    .search__form {
        padding: 0 15px;
    }
    .search__form--input {
        width: 65%;
    }
    .slick-arrow  {
        width: 40px;
        &--next__img,
        &--prev__img {
            width: 40px;
        }
        &--next {
            right: 60px;
        }
    }
    .popular__title {
        font-size: 36px;
        line-height: 42px;
    }
    .reason {
        &__title {
            font-size: 28px;
            line-height: 32px;
        }
    }
    .best__title {
        font-size: 28px;
        line-height: 32px;
        max-width: 250px;
    }
    .about h4 {
        font-size: 36px;
        line-height: 42px;
    }
   }

   @media (max-width: 440px) {
    .search__form--input {
        width: 100%;
    }
    .search__form--button {
        width: 100%;
        padding: 13px 23px;
        &::before {
            display: none;
        }
      }
      .about {
          &__inner {
              .reviews {
                  text-align: center;
             }
          }
      }
    }

   @media (max-width: 360px) {
       .popular__title {
           font-size: 32px;
           line-height: 38px;
       }
       .reason__title {
           font-size: 24px;
           line-height: 28px;
       }
   }
